/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { createRef } from 'react';
import { ModelViewer, activateAR } from '@r2u/react-ar-components'
import type { ModelViewerElement } from '@r2u/react-ar-components'
import '@google/model-viewer'

function App() {
  const usdz = 'https://wsggbucket.s3.ap-northeast-2.amazonaws.com/toy_drummer.usdz'
  const glb = 'https://wsggbucket.s3.ap-northeast-2.amazonaws.com/Thonker.glb'
  const imageUrl = 'https://wsggbucket.s3.ap-northeast-2.amazonaws.com/bori19.jpg'
  const viewerRef = createRef<ModelViewerElement>()
  
  return (
    <div>
      <div>
        <h1>AR</h1>
        <button type="button" onClick={() => activateAR({ glb, usdz })}>
          View in 3D
        </button>
      </div>
      <div>
        <h1>ModelViewer</h1>
        <ModelViewer
          ref={viewerRef}
          src={glb}
          alt="3D model"
          style={{flex:1}}
          poster={imageUrl}
        />
      </div>
    </div>
  )
}

export default App;
